.presale-main {
  background-image: url(/static/media/banner_presale.d43c95c9.jpg);
  background-position: center;
  background-size: cover;
  padding: 0px 2% 50px 2%;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.presale-info {
  margin-left: auto;
  margin-right: auto;
  width: 100%;
}

.presale-text {
  text-align: center;
  font-size: 21px;
  margin: 0px auto 20px auto;
}

.presale-text-width {
  width: 46%;
}

.presale-text-top {
  width: 70%;
  font-size: 24px;
}

@media screen and (max-width: 750px) {
  .presale-column {
    flex-direction: column;
  }
  .presale-text-width {
    width: 90%;
  }
}

@media screen and (max-width: 400px) {
  .presale-small {
    font-size: 16px;
  }
  .presale-small-top {
    font-size: 20px;
  }
  .presale-smaller,
  .dp-pre-label {
    font-size: 12px;
  }
}

.presale-text-container {
  margin-top: 40px;
  display: flex;
  justify-content: space-evenly;
  align-items: flex-start;
  flex-basis: 100%;
  flex-wrap: wrap;
}

.dp-pre-label {
  display: inline-block;
  width: calc(60% + 5px);
  text-align: left;
  font-size: 18px;
}

.dp-pre-form {
  margin-top: 32px;
  width: 100%;
}

.dp-pre-btn {
  border: black 1px solid;
  background-color: var(--wolves-blue-dk);
  color: var(--wolves-orange);
  height: 40px;
  width: 20%;
  min-width: 60px;
}

.dp-pre-btn:disabled {
  opacity: 0.6;
}

.dp-pre-input {
  font-family: 'vincente', serif;
  font-weight: bold;
  color: var(--wolves-orange);
  margin-right: 5px;
  font-size: 28px;
  height: 40px;
  width: 40%;
  padding: 0px 5px 0px 5px;
  vertical-align: bottom;
}

.dp-pre-claim-container {
  display: flex;
  vertical-align: baseline;
  margin-top: 40px;
  align-items: center;
  justify-content: center;
}

.dp-pre-claim-label {
  display: inline-block;
  width: 40%;
  margin-right: 5px;
  text-align: left;
  font-size: 18px;
}

.progress-form {
  width: 100%;
  display: flex;
  margin-top: 20px;
}

.progress-outer {
  background-color: var(--wolves-blue-dk);
  height: 40px;
  width: 60.5%;
  padding: 6px;
}

.progress-inner {
  background-color: var(--wolves-orange);
  height: 28px;
}

.progress-label {
  font-size: 24px;
  width: 19.75%;
  color: var(--wolves-blue-dk);
}

